html body{

      background-color: #fff;
      color: #07070A;
    }



h2{
  color: #07070A;
}



textarea.form-control,  textarea.form-control:focus, input[type="email"]:focus, input[type="email"]{
    background-color: #2D2D2A;
    color: #CECECE;

}

textarea.form-control:focus, input[type="email"]:focus{
  background-color: #CECECE;
  color: #2D2D2A;
  border-color: #CECECE;
  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.075) inset, 0 0 0px #CECECE;
  outline: 0 none;
}

.btn-invis, .btn-invis:link, .btn-invis:focus{
    color: #CECECE;
    background-color: transparent;
    border-color: #CECECE;
    border-radius: 40px;
    box-shadow:none !important;
    outline: 0 none;
    outline-color: transparent;
      outline-style: none;
}

.btn-invis[type="submit"], .btn-invis:active, .btn-invis[type="button"] {
    color: #CECECE;
    background-color: transparent;
    border-color: #CECECE;
    border-radius: 40px;
    outline: 0 none;
}

.btn-invis:hover, .btn-invis[type="submit"]:hover, .btn-invis[type="submit"]:focus{
  color: #2D2D2A;
  background-color: #CECECE;
  border-color: #CECECE;
  border-radius: 40px;
  box-shadow: 0 0px 0px transparent inset, 0 0 0px #CECECE;
  outline: 0 none;
}


.btn-lux, .btn-lux:link, .btn-lux:focus, .btn-lux:visited{
    color: #CECECE;
    background-color: #07070A;
    border-color: transparent;
    border-radius: 40px;
